@import '../var.scss';
header {
  position: relative;
  width: 100%;
  height: 80px;
  // background-color: $mainColor;
  z-index: 50 !important;
  padding-top: 20px;
  overflow: hidden;
  animation: startHeader 0.6s ease-in forwards;
  @media screen and (max-width: 500px) {
    padding-top: 0px;
  }
  .header__logo {
    position: absolute;
    width: 80px;
    height: 80px;
    display: grid;
    align-content: center;
    // justify-content: center;
    margin-left: 50px;
    .link {
      text-decoration: none;
      color: white;
    }
    h1 {
      text-decoration: none;
      font-size: 50pt;
      color: white;
    }
    @media screen and (max-width: 500px) {
      margin-left: 10px;
    }
  }
  .header__txtMenu {
    position: absolute;
    width: 400px;
    // height: 100%;
    left: auto;
    right: 125px;
    text-align: right;
    margin-top: 24px;
    color: white;
    font-weight: 300;
    .link {
      margin-left: 20px;
      font-size: 20pt;
      text-decoration: none;
      color: white;
      font-weight: 300;
      border-bottom: 1px solid white;
    }
    @media screen and (max-width: 500px) {
      width: 230px;
      margin-top: 30px;
      right: 90px;
      .link {
        font-size: 14pt;
        margin-left: 10px;
      }
    }
    @media screen and (max-width: 320px) {
      width: 180px;
      .link {
        font-size: 10pt;
        margin-left: 10px;
      }
    }
  }
}
// .hid{
//     top: -80px;
//     opacity: 0;
// }
@keyframes startHeader {
  0% {
    top: -80px;
  }
  100% {
    top: 0px;
  }
}
